.brands__item {
    margin-right: 66px;
    margin-bottom: 15px;
    max-width: 156px;
    min-height: 113px;

    &:hover {
        img {
            transform: scale(1.2);
        }

    }

    img {
        display: block;
        max-width: 100%;
        object-fit: cover;
        transition: all .3s ease;
    }

    &:last-child {
        margin-right: 0;
    }
}

@media (max-width: 768px) {

    .brands {

        &__item {
            max-width: 136px;
            min-height: 113px;
            margin: 0;
            padding: 15px;
        }
    }
}

@media (max-width: 425px) {

    .brands {

        &__item {
            max-width: 100%;
            padding: 10px 0;
        }
    }
}