@font-face {
  font-family: 'Poppins';
  font-weight: bold;
  font-style: normal;
  src: url(./fonts/Poppins-Bold.woff) format("woff"), url(./fonts/Poppins-Bold.woff2) format("woff2"), url(./fonts/Poppins-Bold.ttf) format("truetype");
}

@font-face {
  font-family: 'Poppins';
  font-weight: normal;
  font-style: normal;
  src: url(./fonts/Poppins-Regular.woff) format("woff"), url(./fonts/Poppins-Regular.woff2) format("woff2"), url(./fonts/Poppins-Regular.ttf) format("truetype");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  font-family: 'Poppins';
  font-weight: normal;
  font-style: normal;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html {
  box-sizing: border-box;
  -ms-overflow-style: none;
  /* IE 10+ */
  scrollbar-width: none;
  /* Firefox */
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

