.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 24px 0;
    z-index: 10;
    transition: all .2s ease;

    &.active {
        background-color: #50553e !important;
    }

    &__inner {
        display: flex;
        justify-content: space-between;
    }

    &__logo {
        z-index: 10;

        img {
            display: block;
            max-width: 100%;
        }
    }

    &__burger {
        width: 40px;
        height: 40px;
        background-color: #fff;
        border-radius: 50%;
        position: relative;
        cursor: pointer;
        transition: all .3s ease;
        z-index: 10;

        span {
            width: 18px;
            height: 2px;
            background-color: #000;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            transition: all .3s ease;
        }

        &::before,
        &::after {
            content: '';
            width: 18px;
            height: 2px;
            background-color: #000;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            transition: all .3s ease;
        }

        &::after {
            bottom: 14px;

        }

        &::before {
            top: 14px;
        }

        &.active {
            &::before {
                transform: rotate(45deg);
                top: 19px;
                left: 11px;
            }

            &::after {
                transform: rotate(-45deg);
                top: 19px;
                left: 11px;
            }

            span {
                transform: scale(0);
            }
        }

    }
}
