.brands {
    position: absolute;
    bottom: -112.5px;
    right: 0;
    width: 100%;
    z-index: 3;

    &__inner {
        max-width: 1275px;
        display: flex;
        align-items: center;
        background-color: #fff;
        margin-left: auto;
        padding: 60px 0 39px 65px;
        flex-wrap: wrap;
        box-shadow: 50px 50px 100px rgba(0, 0, 0, 0.145947);
        border-radius: 2px;
    }

}

@media (max-width: 1280px) {
    .brands {

        &__inner {
            max-width: 100%;
            padding: 30px 0 15px;
            justify-content: center;
        }
    }
}

@media (max-width: 768px) {

    .brands {

        &__inner {
            padding: 0;
            justify-content: center;

        }
    }
}

@media (max-width: 425px) {

    .brands {
        &__inner {
            flex-direction: column;

        }
    }
}