.intro {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    position: relative;

    &__inner {
        min-height: 811px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        max-width: 610px;
    }

    &__head {
        font-size: 14px;
        line-height: 21px;
        margin-bottom: 22px;
        color: #fff;
    }

    &__title {
        font-size: 60px;
        line-height: 123%;
        color: #fff;
        font-weight: 700;
        margin-bottom: 24px;
    }

    &__text {
        font-size: 16px;
        line-height: 162%;
        color: #fff;
        margin-bottom: 45px;
    }

    &__link {
        max-width: 105px;
    }
}

.link {
    font-size: 16px;
    line-height: 18px;
    padding-left: 7px;
    position: relative;
    display: inline-block;

    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        transition: all .3s ease;
    }

    &::before {
        content: "";
        width: 0;
        height: 0;
        border-top: 7px solid transparent;
        border-bottom: 7px solid transparent;
        position: absolute;
        left: -7px;
        top: 1px;
        border-radius: 2px;
        transition: all .3s ease;
    }

    &_yellow {
        color: #FFD723;

        &::before {
            border-left: 8px solid #FFD723;
        }

        &::after {
            background-color: #FFD723;
        }
    }
}

@media (max-width: 1440px) {
    .intro {
        &__inner {
            min-height: 711px;
        }

        &__title {
            font-size: 48px;
        }
    }
}

@media (max-width: 768px) {
    .intro {
        &__inner {
            padding-top: 120px;
            justify-content: flex-start;
            min-height: 511px;
        }

        &__title {
            font-size: 38px;
        }
    }
}

@media (max-width: 767px) {

    .intro {
        &__inner {
            padding-top: 100px;
        }

        &__title {
            font-size: 24px;
        }
    }
}

@media (max-width: 425px) {

    .intro {
        &__inner {
            min-height: 970px;
        }

    }
}